
.container{
    display: flex;
    padding: 20px 60px;
    background-color: black;
}

.branding{
    margin-right: auto;
    text-decoration: none;
    color: rgb(218, 212, 205);
    font-weight: bold;
}

.header-link{
    color: #dad4cd;
    opacity: 0.5;
    text-decoration: none;
    margin-right: 10px;
    transition: all .2s;
    font-weight: bold;
}

.header-link:hover{
    opacity: 1;
}
.card-header{
    display: flex;
    align-items: center;
}

.card-header h1{
    margin-right: auto;
    color:rgb(102, 102, 102);
}

.card-body{
    width:80%;
    margin: 0 auto;
    border: 1px rgb(221, 221, 221) solid;
    padding: 5px 30px;
    margin-bottom: 30px; 
    cursor: pointer;
}

.card-body h4{
    font-size: 20px;
    
}

.list-group{
    list-style: none;
    padding: 0;
}

.list-group li:not(:last-child){
    padding: 30px 10px;
    border: 1px rgb(184, 184, 184) solid;
}

.list-group li:last-child{
    padding: 30px 10px;
    border: 1px rgb(184, 184, 184) solid;
    border-top: none;
}

.list-item span{
    font-weight: bold;
}

.fas{
    cursor: pointer;
}

.fa-trash-alt{
    color: rgb(100, 100, 100);
}

.fa-edit{
    color: rgb(100, 100, 100);
    margin-right: 10px;
}

.title-container{
    width:80%;
    margin: 0 auto;

}

.contacts-title{
    font-weight: 100;
    font-size: 45px;
}

.contacts-title span{
    color: tomato;
} 
.addcontact-card{
    width: 80%;
    margin: 0 auto;
    border: solid rgb(177, 177, 177) 1px;
    margin: 30px auto;
}
.addcontact-header{
    background-color: rgb(238, 238, 238);
    padding: 10px 20px;
    color: rgb(134, 134, 134);
    
}

.form{
    display: flex;
    flex-direction: column;
    padding: 20px 20px;
    width: 90%;
    margin: 0 auto;
}

.form input{
    padding: 9px 15px;
    margin-bottom: 15px;
    font-size: 19px;
}

.form label{
    margin-bottom: 15px;
    color: rgb(134, 134, 134);
}

.form-button{
    background-color: brown;
    border: none;
    width: 60%;
    margin: 0 0 20px 0;
    position: relative;
    left: 50%;
    -webkit-transform: translateX(-50%);
            transform: translateX(-50%);
    padding: 7px 0;
    color: rgb(230, 230, 230);
    font-size: 17px;
    font-weight: bold;
    cursor: pointer;
}
.about-container{
    width: 80%;
    margin: 20px auto;
}

.about-container h1{
    font-size: 52px;
    font-weight: 200;
}
.notfound-container{
    width: 80%;
    margin: 20px auto;
}
body{
  font-family: Arial, Helvetica, sans-serif;
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

*{
  box-sizing: border-box;
}
